.App {
  text-align: center;
}

.App .box {
  background: red;
  width: 32px;
  height: 32px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App a {
  color: #61dafb;
}

a:link,
a:visited,
a:hover {
  color: black;
  text-decoration: none;
}
